/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  /* Prevent zoom on iOS when input is focused */
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: linear-gradient(135deg, #1e1b4b 0%, #312e81 25%, #4c1d95 50%, #5b21b6 75%, #1e1b4b 100%);
  min-height: 100vh;
  overflow-x: hidden;
  /* Improve mobile performance */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  min-height: 100vh;
  position: relative;
}

/* Animated stars background */
.stars {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: transparent;
  pointer-events: none;
}

.stars::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(2px 2px at 20% 30%, rgba(255, 255, 255, 0.8), transparent),
    radial-gradient(2px 2px at 40% 70%, rgba(255, 255, 255, 0.6), transparent),
    radial-gradient(1px 1px at 90% 40%, rgba(255, 255, 255, 0.8), transparent),
    radial-gradient(1px 1px at 50% 50%, rgba(255, 255, 255, 0.6), transparent),
    radial-gradient(2px 2px at 80% 10%, rgba(255, 255, 255, 0.8), transparent);
  background-size: 550px 550px, 350px 350px, 250px 250px, 150px 150px, 450px 450px;
  animation: sparkle 20s linear infinite;
}

@keyframes sparkle {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  50% { transform: translateY(-10px) rotate(180deg); }
}

/* Login Page Styles */
.login-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.login-container {
  width: 100%;
  max-width: 400px;
  padding: 2rem;
}

.login-card {
  background: rgba(30, 27, 75, 0.95);
  border: 1px solid rgba(139, 92, 246, 0.3);
  backdrop-filter: blur(10px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
  animation: fadeInUp 0.8s ease-out;
}

.login-title {
  font-size: 2rem;
  font-weight: 700;
  background: linear-gradient(45deg, #8b5cf6, #a855f7, #c084fc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 0.5rem;
}

.login-hint {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  font-style: italic;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.magical-input {
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.3);
  color: white;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.magical-input:focus {
  border-color: #8b5cf6;
  box-shadow: 0 0 0 3px rgba(139, 92, 246, 0.2);
  transform: translateY(-1px);
}

.magical-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.unlock-btn {
  background: linear-gradient(45deg, #8b5cf6, #a855f7);
  border: none;
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: none;
}

.unlock-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(139, 92, 246, 0.4);
  background: linear-gradient(45deg, #7c3aed, #9333ea);
}

.error-message {
  color: #f87171;
  font-size: 0.9rem;
  text-align: center;
  animation: shake 0.5s ease-in-out;
}

/* Main Page Styles */
.main-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.main-container {
  width: 100%;
  max-width: 900px;
  padding: 2rem;
}

.main-title {
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 2rem;
  background: linear-gradient(45deg, #8b5cf6, #a855f7, #c084fc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: fadeInUp 0.8s ease-out;
}

.video-container {
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto 2rem;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
}

.birthday-video {
  width: 100%;
  height: auto;
  display: block;
}

.video-placeholder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(30, 27, 75, 0.9), rgba(49, 46, 129, 0.9));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.2rem;
  text-align: center;
  aspect-ratio: 16/9;
}

.play-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  animation: pulse 2s infinite;
}

.continue-section {
  text-align: center;
  animation: fadeInUp 0.8s ease-out;
}

.continue-text {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  font-style: italic;
}

.continue-btn {
  background: linear-gradient(45deg, #8b5cf6, #a855f7);
  border: none;
  color: white;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: none;
}

.continue-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(139, 92, 246, 0.4);
}

/* Text Page Styles */
.text-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
  padding: 2rem;
}

.text-container {
  width: 100%;
  max-width: 800px;
}

.message-card {
  background: rgba(30, 27, 75, 0.95);
  border: 1px solid rgba(139, 92, 246, 0.3);
  backdrop-filter: blur(10px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.8s ease-out;
}

.message-card.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.message-content {
  padding: 2.5rem;
}

.message-title {
  font-size: 2.5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 2rem;
  background: linear-gradient(45deg, #8b5cf6, #a855f7, #c084fc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.message-text {
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.8;
  font-size: 1.1rem;
}

.message-paragraph {
  margin-bottom: 1.5rem;
  text-align: justify;
}

.message-signature {
  text-align: right;
  font-style: italic;
  color: #c084fc;
  font-size: 1.2rem;
  margin-top: 2rem;
}

/* Question Page Styles */
.question-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.question-container {
  width: 100%;
  max-width: 600px;
  padding: 2rem;
}

.question-card {
  background: rgba(30, 27, 75, 0.95);
  border: 1px solid rgba(139, 92, 246, 0.3);
  backdrop-filter: blur(10px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
  animation: fadeInUp 0.8s ease-out;
}

.question-content {
  padding: 2.5rem;
  text-align: center;
}

.question-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  background: linear-gradient(45deg, #8b5cf6, #a855f7, #c084fc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.question-text {
  margin-bottom: 2rem;
}

.question-main {
  font-size: 1.4rem;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 1rem;
  font-weight: 600;
}

.question-sub {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.7);
  font-style: italic;
}

.escape-message {
  color: #c084fc;
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
  font-style: italic;
  animation: fadeIn 0.5s ease-out;
}

.disappear-message {
  color: #c084fc;
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  font-style: italic;
  font-weight: 600;
  animation: fadeInBounce 0.8s ease-out;
  text-align: center;
}

@keyframes fadeInBounce {
  0% {
    opacity: 0;
    transform: translateY(10px) scale(0.8);
  }
  50% {
    opacity: 1;
    transform: translateY(-5px) scale(1.05);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.buttons-container {
  display: flex;
  gap: 2rem;
  justify-content: center;
  align-items: center;
}

.yes-btn {
  background: linear-gradient(45deg, #10b981, #059669);
  border: none;
  color: white;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: none;
}

.yes-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(16, 185, 129, 0.4);
  background: linear-gradient(45deg, #059669, #047857);
}

.no-btn {
  background: linear-gradient(45deg, #ef4444, #dc2626);
  border: none;
  color: white;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: none;
}

.no-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(239, 68, 68, 0.4);
}

/* Final Page Styles */
.final-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.celebration-container {
  width: 100%;
  max-width: 800px;
  padding: 2rem;
  position: relative;
}

.floating-hearts {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.floating-hearts .heart {
  position: absolute;
  font-size: 2rem;
  animation: float 6s ease-in-out infinite;
  opacity: 0.8;
}

.floating-hearts .heart:nth-child(1) {
  left: 10%;
  animation-delay: 0s;
}

.floating-hearts .heart:nth-child(2) {
  left: 20%;
  animation-delay: 1s;
}

.floating-hearts .heart:nth-child(3) {
  left: 50%;
  animation-delay: 2s;
}

.floating-hearts .heart:nth-child(4) {
  left: 80%;
  animation-delay: 3s;
}

.floating-hearts .heart:nth-child(5) {
  left: 90%;
  animation-delay: 4s;
}

.final-card {
  background: rgba(30, 27, 75, 0.95);
  border: 1px solid rgba(139, 92, 246, 0.3);
  backdrop-filter: blur(10px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 2;
  animation: fadeInUp 0.8s ease-out;
}

.final-card.celebrate {
  animation: celebrate 1s ease-out;
}

.final-content {
  padding: 2.5rem;
  text-align: center;
}

.celebration-title {
  font-size: 4rem;
  font-weight: 700;
  margin-bottom: 2rem;
  background: linear-gradient(45deg, #10b981, #059669, #34d399);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: bounce 2s infinite;
}

.celebration-text {
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 2rem;
  font-weight: 600;
}

.final-text-content {
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.8;
  font-size: 1.1rem;
  margin-bottom: 2rem;
  text-align: left;
}

.final-text-content p {
  margin-bottom: 1.5rem;
}

.final-signature {
  text-align: right;
  font-style: italic;
  color: #c084fc;
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.restart-btn {
  background: linear-gradient(45deg, #8b5cf6, #a855f7);
  border: none;
  color: white;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: none;
}

.restart-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(139, 92, 246, 0.4);
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  10%, 30%, 50%, 70%, 90% { transform: translateX(-5px); }
  20%, 40%, 60%, 80% { transform: translateX(5px); }
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

@keyframes float {
  0%, 100% { transform: translateY(0) rotate(0deg); }
  50% { transform: translateY(-20px) rotate(180deg); }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-10px); }
  60% { transform: translateY(-5px); }
}

@keyframes celebrate {
  0% { transform: scale(1); }
  50% { transform: scale(1.02); }
  100% { transform: scale(1); }
}

/* Mobile-First Responsive Design */
@media (max-width: 768px) {
  body {
    font-size: 16px;
  }
  
  /* Login Page Mobile */
  .login-container {
    padding: 1rem;
    max-width: 90%;
  }
  
  .login-card {
    padding: 0;
  }
  
  .login-title {
    font-size: 1.8rem;
    line-height: 1.2;
  }
  
  .login-hint {
    font-size: 0.85rem;
    padding: 0 0.5rem;
  }
  
  .magical-input {
    font-size: 16px; /* Prevents zoom on iOS */
    padding: 1rem;
    border-radius: 10px;
  }
  
  .unlock-btn {
    padding: 1rem 1.5rem;
    font-size: 1.1rem;
    border-radius: 10px;
    min-height: 48px; /* Touch-friendly */
  }
  
  /* Main Page Mobile */
  .main-container {
    padding: 1rem;
  }
  
  .main-title {
    font-size: 2.2rem;
    line-height: 1.2;
    margin-bottom: 1.5rem;
  }
  
  .video-container {
    margin-bottom: 1.5rem;
    border-radius: 10px;
  }
  
  .video-placeholder {
    padding: 2rem 1rem;
  }
  
  .play-icon {
    font-size: 3rem;
    margin-bottom: 0.5rem;
  }
  
  .continue-text {
    font-size: 1.1rem;
    margin-bottom: 1rem;
    padding: 0 1rem;
  }
  
  .continue-btn {
    padding: 1rem 2rem;
    font-size: 1.1rem;
    border-radius: 10px;
    min-height: 48px;
  }
  
  /* Text Page Mobile */
  .text-container {
    padding: 1rem;
  }
  
  .message-content {
    padding: 1.5rem;
  }
  
  .message-title {
    font-size: 2rem;
    line-height: 1.2;
    margin-bottom: 1.5rem;
  }
  
  .message-text {
    font-size: 1rem;
    line-height: 1.6;
  }
  
  .message-paragraph {
    margin-bottom: 1.2rem;
    text-align: left; /* Better for mobile reading */
  }
  
  .message-signature {
    font-size: 1.1rem;
    margin-top: 1.5rem;
  }
  
  /* Question Page Mobile */
  .question-container {
    padding: 1rem;
  }
  
  .question-content {
    padding: 1.5rem;
  }
  
  .question-title {
    font-size: 2rem;
    line-height: 1.2;
    margin-bottom: 1.5rem;
  }
  
  .question-main {
    font-size: 1.2rem;
    line-height: 1.3;
    margin-bottom: 1rem;
  }
  
  .question-sub {
    font-size: 0.9rem;
    line-height: 1.4;
    padding: 0 0.5rem;
  }
  
  .escape-message {
    font-size: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .disappear-message {
    font-size: 1.1rem;
    margin-bottom: 1.5rem;
  }
  
  .buttons-container {
    flex-direction: column;
    gap: 1.5rem;
    padding: 0 1rem;
  }
  
  .yes-btn,
  .no-btn {
    padding: 1.2rem 2rem;
    font-size: 1.2rem;
    border-radius: 10px;
    min-height: 54px; /* Extra touch-friendly */
    width: 100%;
    max-width: 250px;
  }
  
  /* Final Page Mobile */
  .celebration-container {
    padding: 1rem;
  }
  
  .final-content {
    padding: 1.5rem;
  }
  
  .celebration-title {
    font-size: 2.5rem;
    line-height: 1.1;
    margin-bottom: 1.5rem;
  }
  
  .celebration-text {
    font-size: 1.3rem;
    margin-bottom: 1.5rem;
  }
  
  .final-text-content {
    font-size: 1rem;
    line-height: 1.6;
    text-align: left;
    margin-bottom: 1.5rem;
  }
  
  .final-text-content p {
    margin-bottom: 1.2rem;
  }
  
  .final-signature {
    font-size: 1.1rem;
    margin-bottom: 1.5rem;
  }
  
  .restart-btn {
    padding: 1rem 2rem;
    font-size: 1.1rem;
    border-radius: 10px;
    min-height: 48px;
  }
  
  /* Mobile Stars Animation - Reduced for performance */
  .stars::before {
    animation-duration: 30s; /* Slower on mobile */
  }
  
  /* Floating Hearts Mobile */
  .floating-hearts .heart {
    font-size: 1.5rem;
    animation-duration: 8s; /* Slower on mobile */
  }
}

/* Extra Small Mobile (iPhone SE, etc.) */
@media (max-width: 390px) {
  .login-container,
  .main-container,
  .text-container,
  .question-container,
  .celebration-container {
    padding: 0.5rem;
  }
  
  .login-title {
    font-size: 1.6rem;
  }
  
  .main-title {
    font-size: 1.8rem;
  }
  
  .message-title,
  .question-title {
    font-size: 1.7rem;
  }
  
  .celebration-title {
    font-size: 2rem;
  }
  
  .message-content,
  .question-content,
  .final-content {
    padding: 1rem;
  }
  
  .video-placeholder {
    padding: 1.5rem 0.5rem;
  }
  
  .play-icon {
    font-size: 2.5rem;
  }
  
  .buttons-container {
    padding: 0;
  }
  
  .yes-btn,
  .no-btn {
    padding: 1rem 1.5rem;
    font-size: 1.1rem;
  }
}

/* Landscape Mobile */
@media (max-width: 768px) and (orientation: landscape) {
  .login-page,
  .main-page,
  .text-page,
  .question-page,
  .final-page {
    padding: 1rem 0;
  }
  
  .main-title {
    font-size: 1.8rem;
    margin-bottom: 1rem;
  }
  
  .message-title,
  .question-title {
    font-size: 1.8rem;
    margin-bottom: 1rem;
  }
  
  .celebration-title {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  
  .video-container {
    max-width: 70%;
  }
  
  .buttons-container {
    flex-direction: row;
    gap: 1rem;
  }
  
  .yes-btn,
  .no-btn {
    width: auto;
    min-width: 120px;
  }
}